import React, { PureComponent } from 'react';
import { numericFormat } from '../lib/utils';

class NumberIncrement extends PureComponent {
  state = {
    value: 0,
    animating: false,
  }

  componentDidMount() {
    this.initializeObserver();
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.total !== nextProps.total) {
      this.initializeObserver();
    }
  }

  componentWillUnmount() {
    this.io.unobserve(this.el);
    if (this.it) {
      clearInterval(this.it);
    }
  }

  initializeObserver = () => {
    this.io = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          this.startAnimation();
        }
      },
      {
        threshold: [0.5],
      },
    );
    this.io.observe(this.el);
  }

  props: {
    total: number,
  }

  startAnimation = () => {
    this.setState({ animating: true }, () => {
      const increment = this.props.total / 10;
      if (this.it) {
        clearInterval(this.it);
      }
      this.it = setInterval(() => {
        if (this.state.value + increment >= this.props.total) {
          clearInterval(this.it);
          this.setState({ value: this.props.total });
          return;
        }
        this.setState({ value: Math.ceil(this.state.value + increment) });
      }, 100);
    });
  }

  el: HTMLElement;

  render() {
    return (
      <span
        ref={(ref) => {
          this.el = ref;
        }}
      >
        {numericFormat(this.state.value)}
      </span>
    );
  }
}

export default NumberIncrement;
