const PercentSkill = ({ skill }) => {
  return (
    <div className="skills">
      <div className="skill">
        <Typography className="lang" variant="body1">{skill.languaje}</Typography>
        <div className="barPercent">
          <div className="barNoComplete" />
          <div className="bar" style={{ width: `${skill.percent}%` }} />
          <span className="percent" style={{ left: `${skill.percent}%` }}>
            <NumberIncrement total={skill.percent} /><span>%</span>
          </span>
        </div>
      </div>
      {skill.adds && (
        <Typography className="addition" variant="body2">{skill.adds}</Typography>
      )}
    </div>
  )
}

export default PercentSkill