import React, { useState } from 'react'
import './cv.scss'
import { Card, CardContent, CardMedia, Divider, Grid, Link, Typography } from "@material-ui/core"

const Works = ({ work }) => {
  const [isFull] = useState(work.imgDesktop && work.imgMobile)

  return (
    <Grid item md={4} sm={6} xs={12}>
      <Card>
        <CardMedia>
          <div className={`contentResponsive ${isFull ? 'isFull' : 'onlyOne'}`} onClick={() => window.open(work.domain, '_blank')}>
            {work.imgDesktop && (<img src={work.imgDesktop} alt={work.name} className={`imgDesktop`} />)}
            {work.imgMobile && (<img src={work.imgMobile} alt={work.name} className={`imgMobile`} />)}
          </div>
        </CardMedia>
        <Divider />
        <CardContent>
          <Link href={work.domain} target="_blank" className="nameWork">
            {work.name}
          </Link>
          <Typography variant="body2">
            {work.technologies.join(", ")}
          </Typography>
        </CardContent>
      </Card>
    </Grid>
  )
}

export default Works