import lockr from 'lockr'

export const CHANGE_THEME = 'CHANGE_THEME'
export const SET_THEME = 'SET_THEME'

export const setTheme = () => (dispatch) => {
  let theme = lockr.get('theme')
  if(theme === undefined){
    theme = false
    lockr.set('theme', theme)
  }
  return dispatch({
    type: SET_THEME,
    payload: theme
  })
}

export const changeTheme = () => {
  let theme = lockr.get('theme')
  lockr.set('theme', theme === undefined ? false : !theme)
  return {
    type: CHANGE_THEME
  }
};
