import { CHANGE_THEME, SET_THEME } from '../actions/themeActions';

const initialState = {
  theme: false
};

function theme(state = initialState, action) {
  switch (action.type) {
    case SET_THEME:
      return {
        theme: action.payload
      }
    case CHANGE_THEME:
      return {
        theme: !state.theme
      };

    default:
      break;
  }
  return state;
}

export default theme;
