import React from 'react'
import { Link, Tooltip, Zoom } from '@material-ui/core'
import './cv.scss'
import { myData } from './dataPersonal'
import WhatsAppIcon from '@material-ui/icons/WhatsApp'
import EmailIcon from '@material-ui/icons/Email'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import GitHubIcon from '@material-ui/icons/GitHub'
import DescriptionIcon from '@material-ui/icons/Description'
import { cv } from '../assets'
import useGAEventsTracker from '../lib/useGAEventsTracker'

const Footer = () => {

  const externalLinks = useGAEventsTracker("External Links")

  return (
    <div className="footer">
      <Tooltip title="WhatsApp" placement="top" TransitionComponent={Zoom} arrow>
        <Link href={`https://api.whatsapp.com/send?phone=${myData.whatsapp}`} target="_blank" onClick={() => externalLinks("WhatsApp", `https://api.whatsapp.com/send?phone=${myData.whatsapp}`)}>
          <WhatsAppIcon fontSize="large" color="action" />
        </Link>
      </Tooltip>
      <Tooltip title="Correo electrónico" placement="top" TransitionComponent={Zoom} arrow>
        <Link href={`mailto:${myData.email}`} target="_blank" onClick={() => externalLinks("Correo electrónico", `mailto:${myData.email}`)}>
          <EmailIcon fontSize="large" color="action" />
        </Link>
      </Tooltip>
      <Tooltip title="Linkedin" placement="top" TransitionComponent={Zoom} arrow>
        <Link href={`${myData.linkedin}`} target="_blank" onClick={() => externalLinks("Linkedin", `${myData.linkedin}`)}>
          <LinkedInIcon fontSize="large" color="action" />
        </Link>
      </Tooltip>
      <Tooltip title="Git" placement="top" TransitionComponent={Zoom} arrow>
        <Link href={`${myData.page}`} target="_blank" onClick={() => externalLinks("Git", `${myData.page}`)}>
          <GitHubIcon fontSize="large" color="action" />
        </Link>
      </Tooltip>
      <Tooltip title="Descargar CV" placement="top" TransitionComponent={Zoom} arrow>
        <Link href={`${cv}`} target="_blank" onClick={() => externalLinks("Descargar CV", `${cv}`)}>
          <DescriptionIcon fontSize="large" color="action" />
        </Link>
      </Tooltip>
    </div>
  )
}

export default Footer