import React from 'react'
import { Typography } from '@material-ui/core'
import { stylesCv } from '../styles/cv.styles'

const TitleSection = ({title, icon}) => {

  const style = stylesCv();

  return (
    <div className={style.contentTitleSection}>
      <img src={icon} className={style.imgTitleSection} alt={title} />
      <Typography className={style.titleSection} noWrap >
        {title}
      </Typography>
    </div>
  )
}

export default TitleSection