import React from 'react'
import { Typography } from '@material-ui/core'
import { connect } from 'react-redux'
import Switch from "react-switch"
import Brightness7Icon from '@material-ui/icons/Brightness7';
import Brightness3Icon from '@material-ui/icons/Brightness3';
import { changeTheme } from '../redux/actions/themeActions'
import { stylesCv, iconSwitch } from '../styles/cv.styles'

const TopHead = ({theme, handleTheme}) => {

  const style = stylesCv();

  return (
    <div className={`${style.top} ${style.withBorder}`}>
      <Typography variant='h3' className={style.titlePrincipal} >Camilo Andres Garzón Pinzón</Typography>
      <Switch
        onChange={handleTheme}
        checked={theme}
        onColor="#FFFFFF"
        onHandleColor="#6d6d6d"
        offColor="#a79f9f"
        offHandleColor="#dcc209"
        handleDiameter={30}
        uncheckedIcon={<Brightness7Icon style={{ ...iconSwitch, color: '#dcc209' }} />}
        checkedIcon={<Brightness3Icon style={{ ...iconSwitch, color: '#3c3c3c' }} />}
        boxShadow="0px 1px 2px rgba(0, 0, 0, 0.5)"
        activeBoxShadow="0px 0px 1px 5px rgba(0, 0, 0, 0.2)"
        height={20}
        width={48}
      />
    </div>
  )
}

const mapStateToProps = (state) => ({
  theme: state.theme.theme
})

const mapDispatchToProps = (dispatch) => ({
  handleTheme: () => dispatch(changeTheme())
})

export default connect(mapStateToProps, mapDispatchToProps)(TopHead)